import Vue from 'vue';
import Router from 'vue-router';
import Info from '../components/views/Info.vue';
import Task from '../components/views/Task.vue';
import Chat from '../components/views/Chat.vue';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Info',
      component: Info,
    },
    {
      path: '/task',
      name: 'Task',
      component: Task,
    },
    {
      path: '/chat',
      name: 'Chat',
      component: Chat,
    },
  ],
});
